"use client";
import { Card } from "@/components/ui/card";
import { Pencil } from "lucide-react"; // 👈 Pencil icon import

export default function Goals() {
  const target = 20000;
  const achieved = 12500;
  const percentage = achieved / target; // 0–1
  const angle = percentage * 180; // needle angle

  return (
    <div>
      {/* Heading outside card */}
      <h2 className="text-sm font-semibold text-gray-500 mb-2">Goals</h2>

      <Card className="p-5 rounded-lg shadow-sm bg-white w-full max-w-[325px] h-[235px] flex flex-col justify-between">
        {/* Header */}
        <div className="flex justify-between items-center mb-2">
          <div className="flex items-center gap-2">
            <h2 className="text-lg font-semibold">${target.toLocaleString()}</h2>
            <Pencil size={16} className="text-gray-500 cursor-pointer hover:text-gray-700" />
          </div>
          <span className="text-sm text-gray-500">May, 2025</span>
        </div>

        <div className="border-t border-gray-200 my-2"></div>

        {/* Content */}
        <div className="flex justify-between flex-1">
          {/* Left side text */}
          <div className="space-y-3 text-sm mt-6">
            <p>
              <span className="text-gray-500">Target Achieved </span>
              <span className="font-medium">${achieved.toLocaleString()}</span>
            </p>
            <p>
              <span className="text-gray-500">This month Target </span>
              <span className="font-medium">${target.toLocaleString()}</span>
            </p>
          </div>

          {/* Right side gauge */}
          <div className="relative w-40 h-24">
            {/* Arc */}
            <svg viewBox="0 0 100 50" className="w-full h-full">
              <path
                d="M10 50 A40 40 0 0 1 90 50"
                fill="none"
                stroke="#e5e7eb"
                strokeWidth="8"
              />
              <path
                d="M10 50 A40 40 0 0 1 90 50"
                fill="none"
                stroke="#0d9488"
                strokeWidth="8"
                strokeDasharray={`${percentage * 126} 126`}
              />
            </svg>

            {/* Needle */}
            <div
              className="absolute left-1/2 bottom-0 w-[2px] h-14 bg-[#0d9488] origin-bottom"
              style={{ transform: `rotate(${angle - 90}deg) translateX(-50%)` }}
            ></div>

            {/* Center Dot */}
            <div className="absolute left-1/2 bottom-0 w-3 h-3 bg-[#0d9488] rounded-full transform -translate-x-1/2"></div>

            {/* Achieved value at midpoint */}
            <div className="absolute left-1/2 bottom-8 transform -translate-x-1/2 text-sm font-semibold text-gray-800">
              ${achieved.toLocaleString()}
            </div>

            {/* Min & Max Labels */}
            <div className="absolute left-0 bottom-0 text-xs text-gray-400">
              $0
            </div>
            <div className="absolute right-0 bottom-0 text-xs text-gray-400">
              ${target.toLocaleString()}
            </div>
          </div>
        </div>

        {/* Footer */}
        <div className="text-center text-xs text-gray-500">
          Target vs Achievement
        </div>
      </Card>
    </div>
  );
}
